import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { increaseIndent } from '../actions';

export default function IncreaseIndentMenu(props: MenuItemInnerProps) {
  const { menuItem, locale, controller } = props;
  const { title, ...rest } = menuItem;

  const handleClick = () => controller.run('onAction', increaseIndent());

  return (
    <MenuItem {...rest} onClick={handleClick} role={PluginRoles.indentIncrease}>
      {title || locale?.locale?.rightButtonTitle}
    </MenuItem>
  );
}
